.App {
  text-align: center;
  background-color: #f8f8f8;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  margin-top: 30px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    
    box-shadow: 2px 2px 9px gray;
   
  }
}

.App-header {
  background-color: #f8f8f8;
 
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
